import {users, groups} from './index'

const events = [
  {
    type: 0,
    event: ''
  },
  {
    type: 1,
    event: ''
  },
  {
    type: 2,
    event: ''
  }
]

const activities = users.map((user, index) => {
  return {
    user: Object.assign({}, user, {group: groups[user.groupId]}),
    activity: events[index % events.length],
    template: ''
  }
})

const templates = [
  (user, activity) => { return `${user.name}  <a >${user.group}</a>  <a>${activity.event}</a>` },
  (user, activity) => { return `${user.name}  <a >${user.group}</a>  <a>${activity.event}</a>` },
  (user, activity) => { return `${user.name}  <a >${activity.event}</a> ` }
]

export {activities, templates}



